:root {
    /* Update core color scheme */
    --primary-dark: #1a1a2e;  /* Keep dark base */
    --primary: #16213e;      /* Deep blue */
    --secondary: #0f3460;    /* Lapis lazuli */
    --accent: #1E90FF;       /* Bright lapis instead of pink */
    --text: #ffffff;         /* Keep white */
    --gold: #ffd700;        /* Keep gold */
    --silver: #c0c0c0;      /* Keep silver */
    --bronze: #cd7f32;      /* Keep bronze */
    --success: #4CAF50;     /* Keep success green */
    --error: #f44336;       /* Keep error red for warnings only */
    --gradient: linear-gradient(135deg, var(--secondary), var(--primary-dark));
    --glass: rgba(255, 255, 255, 0.1);
    --locked: #808080;
  
    /* New accent gradients */
    --royal-gradient: linear-gradient(135deg, var(--gold), var(--accent));
    --blue-gradient: linear-gradient(135deg, var(--secondary), var(--accent));
    --gold-gradient: linear-gradient(135deg, var(--gold), #ffa500);
  }
  
  /* Add to existing CSS, preferably in a mobile-specific section */
  * {
      -webkit-tap-highlight-color: transparent; /* Removes flash on tap */
      -webkit-touch-callout: none; /* Prevents default touch actions */
      touch-action: manipulation; /* Improves touch responsiveness */
  }
  
  body, html {
      overscroll-behavior-y: contain; /* Prevents bounce effect on iOS */
      overflow-x: hidden;
      max-width: 100%;
      position: relative;
  }
  
  .screen {
      transition: opacity 0.3s ease-in-out; /* Smoother, shorter transition */
      opacity: 0;
  }
  
  .screen.visible {
      opacity: 1;
  }
  
  
  /* Update auth modal */
  .auth-modal-content {
      background: var(--glass);
      border: 1px solid var(--accent);
  }
  
  
  /* Update side panel */
  .side-panel {
      background: linear-gradient(135deg, 
          rgba(22, 33, 62, 0.95),
          rgba(15, 52, 96, 0.95)
      );
  }
  
  /* Update user profile section */
  .user-profile-section {
      background: linear-gradient(135deg, 
          rgba(255, 255, 255, 0.05), 
          rgba(30, 144, 255, 0.05)
      );
      border: 1px solid rgba(30, 144, 255, 0.1);
  }
  
  /* Update status badges */
  .status-badge.premium {
      background: var(--royal-gradient);
  }
  
  .status-badge.pending {
      background: var(--blue-gradient);
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
  }
  
  body {
    margin: 0;
    padding: 0;
    min-height: 100vh;
    background: var(--gradient);
    color: var(--text);
    overflow-x: hidden;
  }
  
  .screen {
    display: none;
    min-height: 100vh;
    padding: 2rem;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background: radial-gradient(circle at center, var(--secondary) 0%, var(--primary-dark) 100%);
  }
  
  .screen.visible {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    animation: fadeIn 0.5s ease-in-out;
  }
  
  .welcome-title {
    font-size: 4rem;
    font-weight: 700;
    margin-bottom: 2rem;
    background: linear-gradient(to right, var(--gold), var(--accent));
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    text-shadow: 0 2px 10px rgba(0,0,0,0.2);
    animation: shimmer 2s infinite;
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
  }
  
  /* Share modal styles */
  .share-modal {
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: var(--glass);
      backdrop-filter: blur(10px);
      border-radius: 20px;
      z-index: 1000;
      width: 90%;
      max-width: 500px;
      animation: fadeIn 0.3s ease;
      border: 1px solid rgba(255, 255, 255, 0.1);
      box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  }
  .share-modal-content {
      padding: 2rem;
  }
  .share-modal-header {
      color: var(--gold);
      margin-bottom: 1rem;
      font-size: 1.5rem;
      text-align: center;
  }
  .share-modal-description {
      margin-bottom: 1.5rem;
      opacity: 0.8;
      text-align: center;
  }
  .users-list {
      max-height: 300px;
      overflow-y: auto;
      margin: 1rem 0;
      padding-right: 5px;
  }
  .users-list::-webkit-scrollbar {
      width: 5px;
  }
  .users-list::-webkit-scrollbar-thumb {
      background: rgba(255, 255, 255, 0.2);
      border-radius: 5px;
  }
  .user-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0.75rem 1rem;
      margin: 0.5rem 0;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 10px;
      transition: all 0.3s ease;
  }
  .user-item:hover {
      background: rgba(255, 255, 255, 0.15);
      transform: translateY(-2px);
  }
  .user-info {
      display: flex;
      align-items: center;
      gap: 0.75rem;
  }
  .user-info i {
      color: var(--gold);
      opacity: 0.8;
  }
  .modal-close {
      margin-top: 1.5rem;
      width: 100%;
  }
  .small-button {
      padding: 0.5rem 1rem;
      font-size: 0.85rem;
      min-width: 0;
      width: auto;
  }
  /* Updated share button style */
  .start-button.share-with-user-btn {
      padding: 0.5rem 1rem;
      font-size: 0.8rem;
      background: rgba(255, 255, 255, 0.1);
      color: var(--text);
      border: 1px solid rgba(255, 255, 255, 0.2);
      border-radius: 20px;
      transition: all 0.3s ease;
      text-transform: uppercase;
      letter-spacing: 0.5px;
  }
  .start-button.share-with-user-btn:hover {
      transform: translateY(-2px);
      background: var(--accent);
      box-shadow: 0 5px 15px rgba(30, 144, 255, 0.2);
  }
  .fade-out {
      opacity: 0;
      transition: opacity 0.3s ease;
  }
  @keyframes fadeIn {
      from { opacity: 0; transform: translate(-50%, -55%); }
      to { opacity: 1; transform: translate(-50%, -50%); }
  }
  @media (max-width: 768px) {
      .share-modal {
          width: 95%;
      }
      
      .user-item {
          flex-direction: column;
          align-items: flex-start;
          gap: 0.75rem;
      }
      
      .user-item .main-button {
          width: 100%;
      }
  }
  
  .fullscreen-upgrade-page {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--gradient);
    z-index: 2000;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 2rem;
    animation: fadeIn 0.3s ease-in-out;
  }
  
  .upgrade-header {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    margin-bottom: 2rem;
  }
  
  .skip-upgrade-button {
    background: rgba(255,255,255,0.15);
    border: none;
    color: var(--text);
    padding: 0.75rem 1.5rem;
    border-radius: 50px;
    font-size: 0.9rem;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .skip-upgrade-button:hover {
    background: rgba(255,255,255,0.25);
  }
  
  .upgrade-content {
    text-align: center;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 600px;
    width: 100%;
  }
  
  .upgrade-title {
    font-size: 2rem;
    color: var(--gold);
    margin-bottom: 1.5rem;
    animation: floatAnimation 3s ease-in-out infinite;
  }
  
  .upgrade-description {
    font-size: 1.2rem;
    line-height: 1.6;
    margin-bottom: 2rem;
    color: var(--text);
  }
  
  .features-list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 2rem;
    text-align: left;
  }
  
  .feature-item {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  
  .feature-item i {
    color: var(--gold);
    font-size: 1.5rem;
  }
  
  .upgrade-form-container {
    background: rgba(255, 255, 255, 0.1);
    padding: 1.5rem;
    border-radius: 15px;
    width: 100%;
    margin-top: 1rem;
  }
  
  #upgradeForm {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    text-align: left;
  }
  
  #upgradeForm .form-group {
    margin-bottom: 1rem;
  }
  
  #upgradeForm label {
    display: block;
    margin-bottom: 0.5rem;
    color: var(--text);
  }
  
  #upgradeForm input[type="tel"],
  #upgradeForm input[type="text"],
  #upgradeForm select {
    width: 100%;
    padding: 0.75rem;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    color: var(--text);
  }
  
  #upgradeForm input[type="checkbox"] {
    margin-right: 0.5rem;
  }
  
  .upgrade-now-button {
    position: relative;
    z-index: 10; /* Ensure the button is on top of other elements */
    cursor: pointer !important;
    pointer-events: auto !important;
    background: var(--gold);
    color: var(--primary-dark);
    border: none;
    padding: 1.25rem 3rem;
    border-radius: 50px;
    font-size: 1.2rem;
    font-weight: bold;
    transition: all 0.3s ease;
    box-shadow: 0 10px 20px rgba(255, 215, 0, 0.2);
    animation: pulseAnimation 1.5s infinite ease-in-out;
    margin-top: 1rem;
    width: 100%;
    -webkit-appearance: none; /* Fix for iOS */
    -webkit-tap-highlight-color: rgba(0,0,0,0); /* Fix for iOS touch feedback */
  }
  
  .upgrade-now-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 15px 30px rgba(255, 215, 0, 0.4);
  }
  
  .upgrade-now-button:active {
    transform: translateY(1px);
    box-shadow: 0 5px 15px rgba(255, 215, 0, 0.3);
  }
  
  /* Add a visual indicator when the button is clicked */
  .upgrade-now-button:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(255, 215, 0, 0.5);
  }
  
  .upgrade-note {
    text-align: center;
    color: var(--text);
    margin-top: 1rem;
    font-size: 0.9rem;
    opacity: 0.8;
  }
  
  @media (max-width: 768px) {
    .fullscreen-upgrade-page {
      padding: 1.5rem;
    }
    
    .upgrade-title {
      font-size: 1.8rem;
    }
    
    .upgrade-description {
      font-size: 1rem;
    }
    
    .feature-item i {
      font-size: 1.2rem;
    }
    
    .upgrade-now-button {
      padding: 1rem 2rem;
      font-size: 1.1rem;
    }
  }
  
  @media (max-width: 480px) {
    .fullscreen-upgrade-page {
      padding: 1rem;
    }
    
    .upgrade-title {
      font-size: 1.5rem;
    }
    
    .upgrade-form-container {
      padding: 1rem;
    }
    
    #upgradeForm input[type="tel"],
    #upgradeForm input[type="text"],
    #upgradeForm select {
      padding: 0.65rem;
    }
  }
  
  @keyframes floatAnimation {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-10px); }
  }
  
  @keyframes pulseAnimation {
    0%, 100% { transform: scale(1); box-shadow: 0 10px 20px rgba(255, 215, 0, 0.2); }
    50% { transform: scale(1.05); box-shadow: 0 10px 30px rgba(255, 215, 0, 0.4); }
  }
  
  /* Maintain active state for auth tabs */
  .auth-container {
      position: fixed;
      top: 20%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: rgba(0, 0, 0, 0.3);
      backdrop-filter: blur(10px);
      padding: 1rem 2rem;
      border-radius: 1rem;
      display: flex;
      gap: 1rem;
      align-items: center;
      z-index: 100;
      width: 90%;
      max-width: 320px; /* Added to ensure consistent width */
  }
  
  
  .auth-input:focus {
      border-color: var(--accent);
  }
  
  .auth-button:hover {
      background: rgba(233, 69, 96, 0.1);
  }
  
  @keyframes pulse-glow {
      0%, 100% { 
          box-shadow: 0 0 10px var(--accent), 0 0 5px var(--accent); 
          transform: translate(-50%, -50%) scale(1.02);
      }
      50% { 
          box-shadow: 0 0 20px var(--accent), 0 0 10px var(--accent); 
          transform: translate(-50%, -50%) scale(1.05);
      }
  }
  
  .auth-container.highlight {
      animation: pulse-glow 1.5s ease-in-out infinite;
      background: rgba(233, 69, 96, 0.1);
  }
  
  .auth-toggle {
      color: rgba(255, 255, 255, 0.6);
      font-size: 0.85rem;
      background: transparent;
      border: none;
      cursor: pointer;
      transition: color 0.3s ease;
  }
  
  .auth-toggle:hover {
      color: var(--text);
  }
  
  .user-info {
      position: fixed;
      top: 2rem;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 0.5rem;
      z-index: 100;
  }
  
  #userEmail {
      font-size: 1.5rem;
      color: var(--gold);
      margin-bottom: 0.5rem;
  }
  
  .student-mode {
      color: var(--text);
      font-size: 1rem;
      opacity: 0.8;
      display: block;
  }
  
  
  @media (max-width: 768px) {
      .auth-container {
          flex-direction: column;
          padding: 1rem;
          width: 90%;
          max-width: 320px;
      }
  
      .auth-input {
          width: 100%;
      }
  }
  
  
  
  .modal {
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,0.8);
  }
  
  .modal-content {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: var(--glass);
      padding: 2rem;
      border-radius: 20px;
      backdrop-filter: blur(10px);
      text-align: center;
  }
  
  #otpInput {
      padding: 1rem;
      margin: 1rem 0;
      background: rgba(255,255,255,0.1);
      border: none;
      border-radius: 10px;
      color: white;
      font-size: 1.2rem;
      text-align: center;
  }
  
  .leaderboard-entry .name {
      color: var(--gold);
  }
  
  .leaderboard-entry .words {
      color: var(--accent);
  }
  
  .leaderboard-entry .coins {
      color: var(--text);
  }
  
  .stage-selector {
      margin: 2rem 0;
      text-align: left;
      width: 100%;
  }
  
  .stage-checkboxes {
      display: flex;
      flex-wrap: wrap;
      gap: 1.5rem;
      margin: 1rem 0;
      justify-content: center;
  }
  
  .stage-checkboxes label {
      display: flex;
      align-items: center;
      gap: 0.75rem;
      cursor: pointer;
      font-size: 1.2rem;
      padding: 1rem;
      background: rgba(255,255,255,0.1);
      border-radius: 15px;
      transition: all 0.3s ease;
  }
  
  .stage-checkboxes label:hover {
      background: rgba(255,255,255,0.2);
  }
  
  .stage-checkboxes input[type="checkbox"] {
      width: 24px;
      height: 24px;
      cursor: pointer;
  }
  
  .word-goal {
      margin: 2rem 0;
      text-align: center;
  }
  
  #wordGoal {
      padding: 0.75rem;
      border-radius: 10px;
      background: rgba(255,255,255,0.1);
      color: var(--text);
      border: 1px solid rgba(255,255,255,0.2);
      width: 120px;
      font-size: 1.1rem;
      text-align: center;
  }
  
  .navigation-button.initialize-button {
      background: var(--accent);
      color: var(--text);
  }
  
  .navigation-button.initialize-button:hover {
      filter: brightness(1.2);
  }
  
  /* Make leaderboard entries clickable for moderator */
  .moderator .leaderboard-entry {
      cursor: pointer;
  }
  
  .moderator-header {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 1rem;
      margin-bottom: 2rem;
      padding: 2rem;
      background: var(--glass);
      border-radius: 20px;
      backdrop-filter: blur(10px);
  }
  
  .otp-display {
      font-size: 3rem;
      color: var(--gold);
      text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
      letter-spacing: 0.5rem;
      margin: 1rem 0;
      font-weight: bold;
  }
  
  .join-game-button {
      position: relative;
      padding: 0.875rem 2rem;
      border: 2px solid var(--accent);
      background: transparent;
      color: var(--text);
      border-radius: 50px;
      font-size: 1.1rem;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.3s ease;
      margin-top: 2rem;
      animation: pulseGlow 1.5s ease-in-out infinite;
  }
  
  @keyframes pulseGlow {
      0%, 100% { 
          box-shadow: 0 0 10px var(--accent), 0 0 5px var(--accent); 
          transform: scale(1.02);
      }
      50% { 
          box-shadow: 0 0 20px var(--accent), 0 0 10px var(--accent); 
          transform: scale(1.05);
      }
  }
  
  .game-controls {
      display: flex;
      gap: 1rem;
      margin: 1rem 0;
  }
  
  .end-arcade-button {
      background: var(--error);
      color: var(--text);
      border: none;
      padding: 1rem 2rem;
      border-radius: 50px;
      font-size: 1.2rem;
      cursor: pointer;
      transition: all 0.3s ease;
      display: none;
  }
  
  .end-arcade-button:hover {
      transform: translateY(-2px);
      box-shadow: 0 5px 15px rgba(244, 67, 54, 0.4);
  }
  
  .end-arcade-button.visible {
      display: block;
      animation: fadeIn 0.3s ease-in-out;
  }
  
  .leaderboard-entry {
      display: grid;
      grid-template-columns: 1fr 3fr 1fr 1fr;
      align-items: center;
      padding: 1rem;
      margin-bottom: 0.5rem;
      background: rgba(255,255,255,0.05);
      border-radius: 10px;
      transition: all 0.3s ease;
      position: relative;
  }
  
  .leaderboard-entry > div {
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
  }
  
  .leaderboard-entry div[data-username] {
      font-weight: 600;
  }
  
  .leaderboard-entry div[data-words],
  .leaderboard-entry div[data-coins] {
      justify-content: center;
      transition: color 0.3s ease;
  }
  
  .leaderboard-header {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      padding: 1rem;
      background: rgba(255,255,255,0.1);
      border-radius: 10px;
      margin-bottom: 1rem;
      font-weight: bold;
      text-align: center;
  }
  
  .leaderboard-header > div {
      text-align: center;
  }
  
  /* Waiting state adjustments */
  .leaderboard-entry.waiting .player-name {
      grid-column: 2;
  }
  
  .player-status-waiting {
      grid-column: 3 / span 2;
      display: flex;
      justify-content: center;
      width: 100%;
  }
  
  .player-status-waiting .status-text {
      background: rgba(30, 144, 255, 0.1);
      color: var(--accent);
      padding: 0.375rem 1rem;
      border-radius: 12px;
      font-size: 1.2rem;
      text-transform: uppercase;
      letter-spacing: 1px;
      text-align: center;
  }
  
  .waiting-indicator {
      position: absolute;
      right: 50px;
      top: 50%;
      transform: translateY(-50%);
      display: flex;
      align-items: center;
      gap: 9px;
  }
  
  .waiting-indicator .dot {
      width: 6px;
      height: 6px;
      background-color: var(--accent);
      border-radius: 50%;
      opacity: 0.7;
  }
  
  .waiting-indicator .dot:nth-child(1) {
      animation: dotPulse 1.4s infinite ease-in-out;
  }
  
  .waiting-indicator .dot:nth-child(2) {
      animation: dotPulse 1.4s infinite ease-in-out 0.2s;
  }
  
  .waiting-indicator .dot:nth-child(3) {
      animation: dotPulse 1.4s infinite ease-in-out 0.4s;
  }
  
  @keyframes dotPulse {
      0%, 60%, 100% { transform: scale(1); opacity: 0.7; }
      30% { transform: scale(1.5); opacity: 1; }
  }
  
  .completion-overlay {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.8);
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 1000;
  }
  
  .completion-content {
      background: var(--glass);
      backdrop-filter: blur(10px);
      padding: 2rem;
      border-radius: 20px;
      text-align: center;
  }
  
  .completion-content h2 {
      color: var(--gold);
      margin-bottom: 1.5rem;
  }
  
  .arcade-progress {
      position: fixed;
      top: 1rem;
      left: 50%;
      transform: translateX(-50%);
      background: var(--glass);
      padding: 0.5rem 1rem;
      border-radius: 15px;
      backdrop-filter: blur(5px);
  }
  
  .leaderboard-entry.moving-up {
      background: rgba(76, 175, 80, 0.08);
      animation: moveUp 0.8s ease-in-out forwards;
      z-index: 2;
  }
  
  .leaderboard-entry.moving-down {
      background: rgba(244, 67, 54, 0.08);
      animation: moveDown 0.8s ease-in-out forwards;
      z-index: 1;
  }
  
  @keyframes moveUp {
      0% { transform: translateY(65px); }
      100% { transform: translateY(0); }
  }
  
  @keyframes moveDown {
      0% { transform: translateY(-65px); }
      100% { transform: translateY(0); }
  }
  
  .moderator-container {
      display: flex;
      height: 100vh;
      width: 100%;
  }
  
  .session-info-panel {
      width: 25%;
      background: var(--glass);
      padding: 2rem;
      display: flex;
