      right: 0.5rem;
    }
    
    .vertical-nav-container.panel-open {
      right: 85%; /* Adjust based on mobile side-panel width */
    }
    
    .nav-button, .hamburger-button {
      width: 45px;
      height: 45px;
      font-size: 1.1rem;
    }
  }
  
  @media (max-height: 600px) {
    .vertical-nav-container {
      gap: 0.5rem;
    }
    
    .nav-button, .hamburger-button {
      width: 40px;
      height: 40px;
      font-size: 1rem;
    }
  }
  
  /* Shop Screen Styles */
  .shop-container {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    max-width: 800px;
    width: 100%;
    padding: 1rem;
  }
  
  .shop-loadout {
    background: var(--glass);
    backdrop-filter: blur(10px);
    border-radius: 20px;
    padding: 1.5rem;
    text-align: center;
  }
  
  .loadout-perks {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin: 1.5rem 0;
    flex-wrap: wrap;
  }
  
  .perk-slot {
    width: 80px;
    height: 80px;
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
  }
  
  .perk-slot.empty {
    border: 2px dashed rgba(255, 255, 255, 0.3);
  }
  
  .perk-slot:not(.empty) {
    border: 2px solid var(--accent);
    box-shadow: 0 0 15px rgba(30, 144, 255, 0.3);
  }
  
  .perk-slot:hover {
    transform: translateY(-5px);
    background: rgba(255, 255, 255, 0.2);
  }
  
  .perk-slot .fa-plus {
    font-size: 1.5rem;
    color: rgba(255, 255, 255, 0.5);
  }
  
  .perk-slot .perk-icon {
    font-size: 2rem;
    color: var(--gold);
  }
  
  .perk-slot .remove-perk {
    position: absolute;
    top: -8px;
    right: -8px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background: var(--error);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.8rem;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  .perk-slot:hover .remove-perk {
    opacity: 1;
  }
  
  .shop-categories {
    display: flex;
    gap: 1rem;
    justify-content: center;
    flex-wrap: wrap;
    margin: 1rem 0;
  }
  
  .category-button {
    padding: 0.75rem 1.5rem;
    border-radius: 50px;
    background: rgba(255, 255, 255, 0.1);
    border: none;
    color: var(--text);
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .category-button.active {
    background: var(--accent);
    box-shadow: 0 0 15px rgba(30, 144, 255, 0.3);
  }
  
  .category-button:hover {
    transform: translateY(-3px);
    background: rgba(255, 255, 255, 0.2);
  }
  
  .shop-items {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1.5rem;
    max-height: 400px;
    overflow-y: auto;
    padding-right: 1rem;
  }
  
  .shop-item {
    background: var(--glass);
    backdrop-filter: blur(5px);
    border-radius: 15px;
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    transition: all 0.3s ease;
    border: 1px solid rgba(255, 255, 255, 0.1);
    position: relative;
  }
  
  .shop-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  }
  
  .shop-item.locked {
    opacity: 0.7;
  }
  
  .shop-item.locked::before {
    content: '\f023';
    font-family: 'Font Awesome 6 Free';
    font-weight: 900;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 2rem;
    color: rgba(255, 255, 255, 0.5);
    z-index: 1;
  }
  
  .shop-item.locked .shop-item-content {
    filter: blur(3px);
  }
  
  .shop-item.selected {
    border: 2px solid var(--gold);
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.3);
  }
  
  .shop-item-icon {
    font-size: 2.5rem;
    color: var(--gold);
    margin-bottom: 0.5rem;
  }
  
  .shop-item-title {
    font-size: 1.2rem;
    font-weight: 600;
    text-align: center;
    margin-bottom: 0.5rem;
  }
  
  .shop-item-description {
    font-size: 0.9rem;
    text-align: center;
    opacity: 0.8;
    margin-bottom: 0.5rem;
  }
  
  .shop-item-cost {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-weight: 600;
  }
  
  .shop-item-cost i {
    color: var(--gold);
  }
  
  .shop-item-actions {
    display: flex;
    gap: 0.5rem;
    margin-top: 1rem;
  }
  
  .shop-item-button {
    padding: 0.5rem 1rem;
    border-radius: 50px;
    border: none;
    font-size: 0.9rem;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .shop-item-button.buy {
    background: var(--accent);
    color: var(--text);
  }
  
  .shop-item-button.buy:hover {
    background: color-mix(in srgb, var(--accent) 80%, white);
    transform: translateY(-2px);
  }
  
  .shop-item-button.select {
    background: var(--gold);
    color: var(--primary-dark);
  }
  
  .shop-item-button.select:hover {
    background: color-mix(in srgb, var(--gold) 80%, white);
    transform: translateY(-2px);
  }
  
  .shop-item-button.selected {
    background: rgba(255, 255, 255, 0.3);
    color: var(--text);
  }
  
  .shop-item-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    transform: none !important;
  }
  
  .coins-display {
    position: fixed;
    top: 2rem;
    right: 2rem;
    background: var(--glass);
    backdrop-filter: blur(5px);
    padding: 0.75rem 1.5rem;
    border-radius: 50px;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 1.2rem;
    font-weight: 600;
    z-index: 100;
  }
  
  .coins-display i {
    color: var(--gold);
  }
  
  .loadout-info {
    color: rgba(255, 255, 255, 0.7);
    font-size: 0.9rem;
  }
  
  .back-to-menu {
    margin-top: 2rem;
    padding: 0.75rem 2rem;
  }
  
  .perk-category-badge {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 0.25rem 0.5rem;
    border-radius: 5px;
    font-size: 0.7rem;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  .perk-category-badge.basic {
    background: rgba(30, 144, 255, 0.3);
    color: var(--text);
  }
  
  .perk-category-badge.advanced {
    background: rgba(76, 175, 80, 0.3);
    color: var(--text);
  }
  
  .perk-category-badge.premium {
    background: rgba(255, 215, 0, 0.3);
    color: var(--text);
  }
  
  .perk-category-badge.special {
    background: rgba(156, 39, 176, 0.3);
    color: var(--text);
  }
  
  /* Mobile Optimization */
  @media (max-width: 768px) {
    .shop-items {
      grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    }
    
    .perk-slot {
      width: 60px;
      height: 60px;
    }
    
    .shop-item-icon {
      font-size: 2rem;
    }
    
    .shop-item-title {
      font-size: 1rem;
    }
    
    .shop-item-description {
      font-size: 0.8rem;
    }
    
    .shop-item-button {
      padding: 0.4rem 0.8rem;
      font-size: 0.8rem;
    }
  }
  
  